import React, { FC } from "react";
import { Modal as CustomModal, Typography, Button } from "antd";

import styles from "./Main.module.scss";

const { Title } = Typography;
interface Props {
    isActive: boolean;
    isPaused: boolean;
    isGameOver: boolean;
    isGameWin: boolean;
    onClose: () => void;
    onStart: () => void;
    onResume: () => void;
}

const Menu: FC<Props> = ({
    isActive,
    isPaused,
    isGameOver,
    isGameWin,
    onClose,
    onStart,
    onResume,
}) => (
    <CustomModal
        title=""
        visible={isActive}
        footer={null}
        closable={false}
    >
        <div className={styles.buttonContainer}>
            {isGameOver && (
                <Title className={styles.title} level={3}>
                    !
                </Title>
            )}

            {isGameWin && (
                <Title className={styles.title} level={3}>
                    !
                </Title>
            )}

            <Button type="primary" onClick={isPaused ? onResume : onStart}>
                {isPaused ? ""
                    : ` ${(isGameOver || isGameWin) ? "" : ""}`
                }
            </Button>

            <Button type="primary" onClick={onClose}>
                  
            </Button>
        </div>
    </CustomModal>
);

export default Menu;
