import React, { FC } from "react";
import { Input, Button, Form } from "antd";

import { UserOutlined } from "@ant-design/icons";
import Container from "@/components/Container";
import PageMeta from "@/components/PageMeta";
import Password from "@/components/Password";
import Header from "@/components/Header";
import PageLoader from "@/components/PageLoader";

import { routes } from "@/config/routes/routes";
import styles from "./SignIn.module.scss";
import { SignInFieldNames, useSignInForm } from "./SignIn.helpers";
import YandexSignIn from "./YandexSignIn/YandexSignIn.view";

const SignIn: FC = () => {
    const { onFinish, onFinishFailed, isLoading } = useSignInForm();

    return (
        <PageLoader isSpinning={isLoading}>
            <Container>
                <PageMeta title="SignIn" description="SignIn page" />
                <Header currentPath={routes.signIn.path} />
                <div className={styles.formContainer}>
                    <Form
                        name="signIn"
                        onFinish={onFinish}
                        onFinishFailed={() => onFinishFailed}
                        layout="vertical"
                    >
                        <Form.Item
                            name={SignInFieldNames.Login}
                            rules={[
                                {
                                    required: true,
                                    message: " ",
                                },
                            ]}
                        >
                            <Input
                                placeholder=""
                                prefix={<UserOutlined />}
                            />
                        </Form.Item>
                        <Form.Item
                            name={SignInFieldNames.Password}
                            rules={[
                                {
                                    required: true,
                                    message: " ",
                                },
                            ]}
                        >
                            <Password placeholder="" />
                        </Form.Item>
                        <Form.Item>
                            <Button block type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>
                        <Form.Item>
                            <YandexSignIn/>
                        </Form.Item>
                    </Form>
                </div>
            </Container>
        </PageLoader>
    );
};

export default SignIn;
